export default function creatHTML(options) {
    /**
     * PHONE-ENTRY-LOGIN 
     * PHONE-ENTRY-SETTING 
     * PHONE-ENTRY-MASK 
     * PHONE-IFRAME 
     * PHONE-ENTRY-PANEL 
     * PHONE-ENTRY-SWITCH 
     * PHONE-ENTRY-MESSAGE 
     * PHONE-LEFT-STATUS 
     * PHONE-ENTRY-TOGGLE 
     */
    var config = {
        EphoneBar_width: 230,
        callStatusPage_width: parseInt(options.width) - 230
    }
    const { version } = require('../../package.json')
    var setting = `<div id="PHONE-ENTRY-SETTING" data-hide='0'  data-toggle='setting'>
                        <span data-type='close' title=${version}>&times;</span>
                        <span data-type='error'></span>
                        <ul>
                            <li>
                                <span></span>
                                <span>
                                <label><input type="radio" name="pattern" checked value="51"  >&nbsp;&nbsp; </label>
                                <label><input type="radio" name="pattern"  value="52" checked >&nbsp; </label>
                                </span>
                            </li>
                           <!-- <li>
                                <span>wss</span>
                                <span>
                                    <select>
                                        <option value="1" selected>wss://s01.vsbc.com:9060</option>
                                        <option value="2">wss://10.0.0.165:10443</option>
                                    </select> 
                                </span>
                            </li> -->
                            <li>
                                <span>wss</span>
                                <span>
                                    <input data-type='wsUrl' value='wss://s01.vsbc.com:9060' type='text'  list="wsUrl"  placeholder='wss'>
                                    <datalist id="wsUrl">
                                        <option value="wss://s01.vsbc.com:9060">
                                        <option value="wss://10.0.0.165:10443">
                                    </datalist>
                                </span>
                            </li>
                            <li>
                                <span></span>
                                <span>
                                <label><input type="radio"   name="postionButton" value="left" checked>&nbsp;&nbsp; </label>
                                <label><input type="radio"   name="postionButton" value="right">&nbsp; </label>
                                </span>
                            </li>
                            <button data-type='confirm'></button>
                        </ul>
                    </div>`
    //
    var loginPage = ` <div id="PHONE-ENTRY-LOGIN" data-hide='0'  data-toggle='loginPage'>
                        <span data-type='close'>&times;</span>
                        <span data-type='error'></span>
                        <ul>
                            <li>
                                <span></span>
                                <input type="text" data-type="switchnumber" value="02566699734" onkeyup="this.value=this.value.replace(/\\D/g,'')" onpaste="this.value=this.value.replace(/\\D/g,'')">
                            </li>
                            <li>
                                <span></span>
                                <input type="text" data-type='seatnumber' onkeyup="this.value=this.value.replace(/\\D/g,'')" onpaste="this.value=this.value.replace(/\\D/g,'')">
                            </li>
                            <li>
                                <span></span>
                                <input type="password" data-type="password">
                            </li>
                            <li  data-hide='0' data-type='model'>  
                                <span data-type='4' data-hide='0' ></span>
                                <span data-type='5' data-hide='0' >sip</span>
                                <input type="text" data-type="modelnumber" onpaste="this.value=this.value.replace(/\\D/g,'')">
                            </li>
                            <li  style="border:none">
                                <label><span><input type="radio" name="callintype" value='2' checked>&nbsp;VOIP</span></label>
                                <label><span><input type="radio" name="callintype" value='5'>&nbsp;SIP</span></label>
                                <label><span><input type="radio" name="callintype" value='4'>&nbsp;</span></label>
                                <span data-type="loginStatus"><em></em>&nbsp;<span></span></span> 
                            </li>
                            <button data-type='login' data-disabled='1'></button>
                        </ul>
                    </div>`
    // 
    var statusPage = `<div id="PHONE-ENTRY-TOGGLE" data-hide='0'   data-toggle='statusPage'> 
                        <ul>
                            <li data-type="leisure" data-hide='1'    data-disabled='false'><em></em><span></span></li>
                            <li data-type="busy"    data-hide='1'    data-disabled='false' data-status='2'><em></em><span></span></li>
                            <li data-type="logout"  data-hide='1'    data-disabled='false'><span><em>&#xe81c;</em></span><span></span></li>
                        </ul>
                    </div>`
    //
    var selectGroup = `<div id="PHONE-ENTRY-SELECTGROUP" data-hide='0' data-toggle='selectGroup' >
                            <span data-type='close'>&times;</span>
                            <div data-type="introduce">:</div>
                            <ul data-type="groupList" class="option_style">
                            </ul>
                            <button data-type="confirm" data-disabled='true' ></button>
                        </div>`

    //
    var fastPage = `<div id='PHONE-ENTRY-PANEL' data-hide='0'  data-toggle='fastPage'>
                        <span data-type='close'>&times;</span>
                        <div>
                            <input type="text" data-type='input' placeholder="" oninput='this.value=this.value.replace(/[^0-9]/g,"").replace(/ /g,"")'>
                            <button data-type='call'></button>
                        </div> 
                    </div>`

    //
    //
    var callStatusPage = ` <div id='PHONE-LEFT-STATUS'  style='width:${
        config.callStatusPage_width
    }px'>
                                <div data-type='pattern'>
                                    <div  data-hide='0'>
                                        
                                    </div>
                                </div>            
                                <div data-type='rect' >
                                    <div data-hide='0'>
                                        <span data-type='number'></span>
                                        &nbsp;|&nbsp;
                                        <span data-type='status'></span>
                                        <span data-type='times'>00:00:00</span>
                                    </div>
                                </div>
                                <div data-type='planePage'>
                                    <div data-hide='0'>
                                        &#xe627;
                                    </div> 
                                </div> 
                                <div  data-type='incomingStatus'>
                                        <div data-hide='0'>
                                            <div data-type='bg'>
                                                <span data-type='number'></span>
                                                &nbsp
                                                <span data-type='status'></span>
                                            </div>   
                                            <button data-hide="1"  data-disable='1' data-type='answer'></button> 
                                        </div>  
                                </div>
                            </div>
                        `
    // 
    // var buttons = ['1', '2', '3', '4', '5', '6', '7', '8', '9', '*', '0', '#']
    // var str = ''
    // buttons.map((v, i) => {
    //     var marginRight = i % 3 == 0 ? 0 : 16
    //     str += '<span style="display:inline-block;"><input data-type="num" type="button" value="' + v + '"  style="margin-left:' + marginRight + 'px"></span>'
    // })
    // var planePage = `<div id="PHONE-PANEL" data-hide='0'   data-toggle='planePage'>
    //           <span data-type='close'>&times;</span>
    //           <div><input data-type='input' id='PANEL-INPUT' type='text' placeholder="" oninput='this.value=this.value.replace(/[^0-9]/g,"")'></div>
    //           <div>
    //           ${str}
    //           </div>
    //           <div data-type='call' data-hide='0'></div>
    //       </div>`
    //
    var switchPage = `  <div id="PHONE-ENTRY-SWITCH" data-hide='0' data-toggle='switchPage'>
                            <span data-type="error"></span>
                            <span data-type="close">&times;</span>
                            <div>
                                <div data-type='selectGroup'>
                                    <div data-type="select">
                                    <div data-type="select_text"></div>
                                    <div data-type="select_icon">
                                        <div class="arrow-down"></div>
                                    </div>
                                </div>
                                <ul data-type="group" data-hide="0">
                                    
                                </ul>
                                <div data-type="filter"><input type="checkbox"  data-type="checkbox" style='vertical-align: middle;'>&nbsp;</div>
                            </div>
                            <ul data-type="kefuList"  class="option_style">
                            </ul>
                            <button data-type="transfer" data-disabled='1'></button>
                        </div>
                        </div>`
    //
    // var threeSession = `<div id="PHONE-TRILATERA-SESSION" data-hide='0' data-toggle='threeSession'>
    //           <p data-type="status">...</p>
    //           <p>
    //             <span data-type="number">02567893755</span>
    //             <span data-type="img">&#xe76b;</span>
    //             <!-- <span data-type="cancel"></span> -->
    //             <!-- <span data-type="redial"></span> -->
    //             <span data-type="hangup"></span>
    //           </p>
    //           <p>
    //             <span data-type="number">02567893755</span>
    //             <!-- <span data-type="img">&#xe76b;</span> -->
    //             <span data-type="cancel"></span>
    //             <span data-type="redial"></span>
    //             <!-- <span data-type="hangup"></span> -->
    //           </p>
    //         </div>`

    //
    var controlPage = `<ul  id='EphoneBar' style='width:${
        config.EphoneBar_width
    }px'>
            
                    <li  data-phone-key="0" data-phone-type="register"  title="">
                        <!-- &#xe600; &#xe633;-->
                        <em>&#xe63f;</em>
                        <span data-type="toggle" data-hide="0"></span>
                        <div  data-arrow='register' class="arrow-up" data-hide='0'></div>
                        <!---->
                        ${statusPage}
                    </li>

                    <!--<li   data-phone-key="0" data-phone-type="logout" data-hide='0' title="">
                        <em>&#xe7c9;</em>
                    </li>
                    <li class="gray"  data-phone-key="3"  data-phone-type='busy' data-hide='1'  title="">
                        <em>&#xe6a5;</em>
                    </li>
                    <li class="" data-phone-key="3"  data-phone-type='leisure' data-hide='0' title="">
                        <em>&#xe6a6;</em>
                    </li> -->

                    <li class="gray"  data-phone-key="1" data-phone-type='open' data-hide='1' title="">
                        <em >&#xe625;</em>
                        <div  data-arrow='open' class="arrow-up"  data-hide='0'></div>
                    </li>

                    <li class="gray"  data-phone-key="1" data-phone-type='terminate' data-hide='0' style='color:red' title="">
                        <em >&#xe845;</em>
                    </li> 
                    <li class="gray" data-phone-key="2" data-phone-type="switch" title="">
                        <em >&#xe603;</em>
                        <div  data-arrow='switch' class="arrow-up"  data-hide='0'></div>
                    </li>

                    <!--<li class="gray"  data-phone-type='three'>
                        <em></em>
                        <span></span>
                        <div  data-arrow='three' class="arrow-up"  data-hide='0'></div>
                    </li> -->

                    <li class="gray"  data-phone-key="1"  data-phone-type='hold' data-hide='1' title="">
                        <em >&#xe61c;</em>
                    </li>
                    <li class=""  data-phone-key="1"  data-phone-type='unhold' data-hide='0' title="">
                        <em >&#xe846;</em>
                    </li> 

                    <li  data-phone-key="4"  data-phone-type='setting' data-hide='1' title="">
                        <em >&#xe610;</em>
                        <div  data-arrow='setting' class="arrow-up"  data-hide='0'></div>
                    </li>
            </ul>`

    var HTML = `<div id="PHONE-ENTRY-CONTAINER" style="width:${
        options.width
    };height:${options.height};">
                    <div  style="background:${options.background};"width:${
        options.width
    };height:${options.height};">                 
                        <audio autoplay id='peeraudio'></audio>
                        <!---->
                        ${controlPage}
                        <!---->
                        ${callStatusPage}
                    </div> 
                        <!---->
                        ${loginPage}
                        <!---->
                        ${selectGroup}
                        <!---->
                        ${fastPage}
                        <!---->              
                        ${switchPage}
                        <!---->
                        ${setting}
                        <!---->                                                                   
                </div>`
    return HTML
}
